//If no snapshot exists, create one with email, first name and last name
export const generateUserDocument = async (user, additionalData) => {
  if (!user) return;

  const userRef = firestore.doc(`users/${user.uid}`);
  const snapshot = await userRef.get();

  if (!snapshot.exists) {
    const { email, firstName, lastName, papers } = user;
    try {
      await userRef.set({
        firstName,
        lastName,
        email,
        papers,
        ...additionalData
      });
      
    } catch (error) {
      console.error("Error creating user document", error);
    }
  } 
   
  return getUserDocument(user.uid);
};



export const updateUserDocument = async (user, additionalData) => {
  if (!user) return;

  const userRef = firestore.doc(`users/${user.uid}`);
  const snapshot = await userRef.get();

  if (snapshot.exists) {
    const { email, firstName, lastName, papers } = user;
    try {
      await userRef.update({
        firstName,
        lastName,
        email,
        papers,
        ...additionalData
      });
    } catch (error) {
      console.error("Error updating user document", error);
    }
  }
  return getUserDocument(user.uid);
};



//Fetching user data, using the user id
const getUserDocument = async uid => {
  if (!uid) return null;
  try {
    const userDocument = await firestore.doc(`users/${uid}`).get();
    return {
      uid,
      ...userDocument.data()
      
    };
  } catch (error) {
    console.error("Error fetching user", error);
  }
};
